.card {
  background: #fff;
  border-radius: 5px;
  height: 180px;
  margin: 1rem;
  position: relative;
  width: 250px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card a:hover{
  cursor:pointer;
  -ms-transform: scale(1.1); /* IE 9 */
  -webkit-transform: scale(1.1); /* Safari 3-8 */
  transform: scale(1.1); 
}

.img-container {
  height: 175px;
  overflow: hidden;

  /* text-align: center;
  background:#6CADDC; */
}

img {
  width: 108%
}
